<template>
  <div id="app">
    <div class="container-fluid" style="padding: unset">
      <Header img="" :notice-is="noticeIs" school='Go语言实战抽奖系统'></Header>
      <div class="row top-main">
        <div class="col-md-6 col-md-offset-2 mt-2">
          <span class="col-sm-12" style="height:18px;font-size:18px;font-family:PingFang-SC-Bold;font-weight:bold;color:rgba(255,255,255,1);">{{baseInfo.schoolName}} / {{baseInfo.facultyName}}</span>
          <span class="col-sm-2 text-left practical-number">{{baseInfo.studentNum}}</span>
          <span class="col-sm-2 text-left practical-number">{{baseInfo.projectNum}}</span>
          <span class="col-sm-2 text-left practical-number">{{baseInfo.noncommentNum}}</span>
          <span class="col-sm-6 text-left practical-number">{{baseInfo.projectNum}}</span>
          <span class="col-sm-2 text-left practical-name">学生数</span>
          <span class="col-sm-2 text-left practical-name">项目数</span>
          <span class="col-sm-2 text-left practical-name">待点评</span>
          <span class="col-sm-6 text-left practical-name">Q&A</span>
        </div>
        <div v-if="baseInfo.overtimeTask !== 0" class="col-md-2 text-center" style="border: 1px solid #FF4C55;border-radius: 10px;margin-top:100px"><i class="iconfont icongantanhao" style="color: #FF4C55"></i><span style="color: #FF4C55">即将超时任务：{{baseInfo.overtimeTask}}项</span></div>
      </div>
      <div class="row" style="background:rgba(255,255,255,1);box-shadow:0px 3px 15px 0px rgba(0, 0, 0, 0.08);height: 59px;line-height: 59px">
        <div class="col-md-3 col-md-offset-2"  style="cursor: pointer">
          <span @click="goback(-1)">助教中心</span> > <span>项目列表</span>
        </div>
      </div>
      <div class="row pt-4 mt-3">
        <div class="col-md-8 col-md-offset-2 project-list pl-0 pr-0 mb-6">
          <div class="col-md-12  pr-0" style="line-height: 45px;height: 50px;border-bottom: 1px solid #EBEBEB;padding-left: 30px;">
          <div class="col-md-1  pl-0 pr-0" @click="selectCommentStatus('comment')" style="cursor: pointer">
            <span class="col-sm-12 text-center " :class="commentStatus == 'comment'?'active-font-color':'unactive-font-color'">已点评</span>
            <span class="col-sm-4 col-sm-offset-4 text-center" :class="commentStatus == 'comment'?'active-bg-color':'unactive-bg-color'" style="height:2px;border-radius:1px;"></span>
          </div>
            <div class="col-md-1  pl-0 pr-0" @click="selectCommentStatus('uncomment')" style="cursor: pointer;margin-left:35px">
              <span class="col-sm-12 text-center " :class="commentStatus == 'uncomment'?'active-font-color':'unactive-font-color'">未点评</span>
              <span class="col-sm-4 col-sm-offset-4 text-center" :class="commentStatus == 'uncomment'?'active-bg-color':'unactive-bg-color'" style="height:2px;border-radius:1px;"></span>
            </div>
            <!--<div class="col-md-1  pl-0 pr-0" @click="selectCommentStatus('qa')" style="cursor: pointer;margin-left:35px">-->
              <!--<span class="col-sm-12 text-center " :class="commentStatus == 'qa'?'active-font-color':'unactive-font-color'">Q&A</span>-->
              <!--<span class="col-sm-4 col-sm-offset-4 text-center" :class="commentStatus == 'qa'?'active-bg-color':'unactive-bg-color'" style="height:2px;border-radius:1px;"></span>-->
            <!--</div>-->
            <!--<div class="col-md-2 text-center col-md-offset-2 pr-4">-->
              <!--<el-select v-model="schoolSearch" placeholder="全部">-->
                <!--<el-option-->
                  <!--v-for="item in schoolList"-->
                  <!--:key="item.value"-->
                  <!--:label="item.label"-->
                  <!--:value="item.value">-->
                <!--</el-option>-->
              <!--</el-select>-->
            <!--</div>-->
            <!--<div class="col-md-4 text-center">-->
              <!--<input type="text" class="form-control input-search"  placeholder="团队或学生名"><i class="iconfont iconqitasousuo" style="position: absolute;top: 0;right: 0;color: rgb(153, 153, 153); margin-right: 30px;"></i>-->
            <!--</div>-->
          </div>
          <div class="col-md-12 pl-0 pr-0">
            <table class="table table-striped">
              <thead>
              <tr>
                <th>团队/姓名</th>
                <th>课程</th>
                <th>项目</th>
                <th>人数</th>
                <th>成员</th>
                <th>Q&A</th>
                <th v-if="commentStatus == 'uncomment'">待点评项</th>
                <th>操作</th>
              </tr>
              </thead>
              <tbody>
              <tr v-for="(item, key) in tableData[currentPage-1]" v-bind:key="key">
                <td v-cloak>
                  {{item.teamName}}
                </td>
                <td v-cloak>
                  {{item.courseName}}
                </td>
                <td v-cloak>
                  {{item.projectName}}
                </td>
                <td v-cloak>
                  {{item.peopleNum}}
                </td>
                <td v-cloak>
                  {{item.memberName}}
                </td>
                <td v-cloak>
                  {{item.anwserquestionNum}}
                </td>
                <td v-cloak v-if="commentStatus == 'uncomment'">
                  {{item.noncommentNum}}
                </td>
                <td v-cloak>
                  <span style="cursor: pointer;color: #6612CF" @click="gotoProjectDetail(item)">详情</span>&nbsp;/&nbsp;
                  <span style="cursor: pointer;color: #6612CF" @click="gotoReportList(item)">日报</span> /
                  <span :title="item.studentQQ" @click="jumpQQ(item)" style="cursor: pointer;color: #6612CF;">联系学生</span>
                </td>
              </tr>
              </tbody>
            </table>
          </div>
          <div class="col-md-12 text-center"  v-if="pageTotal !== -1" style="margin-bottom: 10px">
            <el-pagination
              background
              layout="prev, pager, next"
              :page-size="pagePer"
              :page-count="pageTotal"
              :current-page="currentPage"
              @current-change="currentPageChange"
              :hide-on-single-page="true"
              :total="dataCount">
            </el-pagination>
          </div>
        </div>
      </div>
      </div>
    </div>
</template>

<script>
import Header from '../../../components/assistant/Header.vue'
import getQueryString from '@/utils/getQueryString'
import { Loading } from 'element-ui'
export default {
  name: 'app',
  components: {
    Header
  },
  mounted () {
    this.loadData()
  },
  data () {
    return {
      schoolId: '', // 学校ID
      facultId: '', // 院系ID
      commentStatus: 'uncomment', // 点评条件状态
      baseInfo: {},
      schoolSearch: '', // 学校（查询条件）
      schoolList: [{
        value: '1',
        label: '东北大学'
      }, {
        value: '2',
        label: '沈阳大学'
      }, {
        value: '3',
        label: '辽宁大学'
      }],
      tableData: [],
      search: '',
      pagePer: 10,
      pageTotal: -1,
      currentPage: 1,
      dataCount: 0,
      noticeIs: 'false'
    }
  },
  methods: {
    loadData () {
      const loading = Loading.service({
        lock: true,
        text: '加载中',
        spinner: 'el-icon-loading',
        // background: 'rgba(0, 0, 0, 0.7)'
        background: 'rgba(f, 0, 0, 0.7)',
        color: '#6612cf'
      })
      let me = this
      if (localStorage.getItem('unreadStatus') === 'false') {
        me.noticeIs = false
      } else {
        me.noticeIs = true
      }
      me.schoolId = getQueryString('schoolId')
      me.facultId = getQueryString('facultId')
      me.$http.post('/assistant/firstpage', { 'schoolId': me.schoolId, 'facultyId': me.facultId }, res => {
        // console.log(res.data.initproject[0])
        if (res.success) {
          me.baseInfo = res.data.initproject[0]
        }
      })
      me.$http.post('/assistant/noncommont', { 'schoolId': this.schoolId, 'facultyId': this.facultId, 'token': localStorage.getItem('token') }, res => {
        console.log(res.data)
        if (res.success) {
          res.data = me.formatTableData(res.data)
          me.dataCount = res.data.length
          me.pageTotal = parseInt(res.data.length / me.pagePer)
          if (res.data.length % me.pagePer === 0) {
            me.pageTotal = parseInt(res.data.length / me.pagePer)
          } else {
            me.pageTotal = parseInt(res.data.length / me.pagePer + 1)
          }
          for (var i = 0; i < me.pageTotal; i++) {
            me.tableData.push(res.data.slice(i * me.pagePer, i * me.pagePer + me.pagePer - 1))
          }
          setTimeout(() => {
            loading.close()
          }, 1000)
        }
      })
    },
    selectCommentStatus (status) {
      let me = this
      this.commentStatus = status
      const loading = Loading.service({
        lock: true,
        text: '加载中',
        spinner: 'el-icon-loading',
        // background: 'rgba(0, 0, 0, 0.7)'
        background: 'rgba(f, 0, 0, 0.7)',
        color: '#6612cf'
      })
      if (this.commentStatus === 'comment') {
        me.$http.post('/assistant/commont', { 'schoolId': this.schoolId, 'facultyId': this.facultId, 'token': localStorage.getItem('token') }, res => {
          if (res.success) {
            res.data = me.formatTableData(res.data)
            me.dataCount = res.data.length
            me.pageTotal = parseInt(res.data.length / me.pagePer)
            if (res.data.length % me.pagePer === 0) {
              me.pageTotal = parseInt(res.data.length / me.pagePer)
            } else {
              me.pageTotal = parseInt(res.data.length / me.pagePer + 1)
            }
            me.tableData = []
            for (var i = 0; i < me.pageTotal; i++) {
              me.tableData.push(res.data.slice(i * me.pagePer, i * me.pagePer + me.pagePer - 1))
            }
            setTimeout(() => {
              loading.close()
            }, 1000)
          }
        })
      } else if (this.commentStatus === 'uncomment') {
        me.$http.post('/assistant/noncommont', { 'schoolId': this.schoolId, 'facultyId': this.facultId, 'token': localStorage.getItem('token') }, res => {
          if (res.success) {
            res.data.noncommentNum = 0
            res.data = me.formatTableData(res.data)
            me.dataCount = res.data.length
            me.pageTotal = parseInt(res.data.length / me.pagePer)
            if (res.data.length % me.pagePer === 0) {
              me.pageTotal = parseInt(res.data.length / me.pagePer)
            } else {
              me.pageTotal = parseInt(res.data.length / me.pagePer + 1)
            }
            me.tableData = []
            for (var i = 0; i < me.pageTotal; i++) {
              me.tableData.push(res.data.slice(i * me.pagePer, i * me.pagePer + me.pagePer - 1))
            }
            setTimeout(() => {
              loading.close()
            }, 1000)
          }
        })
      }
      this.currentPage = 1
    },
    formatTableData (data) {
      for (var i = 0; i < data.length; i++) {
        if (data[i].memberName !== null) {
          let members = data[i].memberName.split(',')
          if (members.length > 2) {
            data[i].memberName = members[0] + ',' + members[1] + '...'
          }
        }
      }
      return data
    },
    currentPageChange (val) {
      this.currentPage = val
    },
    goback (index) {
      window.history.go(index)
    },
    gotoProjectDetail (obj) {
      window.location = 'assistant-task-list.html?teamId=' + obj.teamId + '&currentStepId=' + obj.defaulMission + '&projectId=' + obj.projectId + '&facultyMarjorId=' + obj.facultyMarjorId
    },
    gotoReportList (obj) {
      window.location = 'assistant-report-list.html?teamId=' + obj.teamId + '&currentStepId=' + obj.defaulMission + '&projectId=' + obj.projectId + '&facultyMarjorId=' + obj.facultyMarjorId
    },
    jumpQQ (obj) {
      window.location = 'tencent://message/?Menu=yes&uin=' + obj.studentQQ + '&Site=1111&Service=300&sigT=45a1e5847943b64c6ff3990f8a9e644d2b31356cb0b4ac6b24663a3c8dd0f8aa12a595b1714f9d45'
    }
  }
}
</script>
<style scoped>
  @import "../../../assets/css/bootstrap.css";
  @import "../../../assets/css/theme.css";
  @import "../../../assets/css/text.css";
  .top-main{
    background-image: url(../../../assets/img/assistant_bg.png);
    background-size: 100% 100%;
    height: 138px;
  }
  .project-list{
    min-height:100px;
    background:rgba(255,255,255,1);
    box-shadow:0px 2px 15px 0px rgba(0, 0, 0, 0.08);
