    font-weight:bold;
    color: #6612CF;
  }
  .unactive-font-color{
    height:16px;
    font-size:16px;
    font-family:PingFang-SC-Regular;
    font-weight:400;
    color:rgba(102,102,102,1);
  }
  .active-bg-color{
    background-color: #6612CF;
  }
  .unactive-bg-color{
    background-color: white;
    display: none;
  }
  td{
    text-align: center;
    border: none!important;
    height: 56px;
    line-height: 56px!important;
  }
  th{
    text-align: center;
    height: 56px;
    line-height: 56px!important;
  }
  .input-search{
    height:30px;
    background:rgba(248,248,248,1);
    border:1px solid rgba(235,235,235,1);
    border-radius:10px;
    border: none;
    margin-top: 7px;
  }
</style>
<style>
  .el-tabs__item:hover, .el-tabs__item.is-active {
    color: #6612CF!important;
    cursor: pointer;
  }
  .el-tabs__active-bar{
    background-color: #6612CF!important;
  }
  .el-input--suffix .el-input__inner {
    background: #F8F8F8!important;
    height: 30px!important;
    border-radius: 10px!important;
  }
  .el-pagination.is-background .el-pager li:not(.disabled).active  {
    background: #6612cf!important;
  }
  .el-loading-spinner i {
    color: #6612cf;
  }
  .el-loading-spinner .el-loading-text {
    color: #6612cf;
    margin: 3px 0;
    font-size: 14px;
  }
</style>
